<script>
    import slogan from '@/components/slogan.vue'
    import company from '@/components/company.vue'
    import {companyInformation} from '@/request/api.js'

    export default {
        components: {
            slogan,
            company
        },
        data () {
            return {
                info: {}
            }
        },
        created () {
            window.scrollTo(0,0);
            this.companyInformation()
        },
        computed: {
            content () {
                if(this.info.content) {
                    return this.showHtml(this.info.content);
                }
                
            }
        },
        methods: {
            companyInformation () {
                let that = this;
                let data = {};
                companyInformation(data).then(res => {
                    if(res.code == 200) {
                        that.info = res.data
                    }
                })
            },
            showHtml:function(str){
                return str
                .replace(str ? /&(?!#?\w+;)/g : /&/g, '&amp;')
                .replace(/&lt;/g,"<")
                .replace(/&gt;/g,">")
                .replace(/&quot;/g,"\"")
                .replace(/&#39;/g, "\'")
                .replace(/&amp;nbsp;/g, "");
            }
        },
    }
</script>

