<script>
    import slogan from '@/components/slogan.vue'
    import company from '@/components/company.vue'
    import {camearStart,camearStop} from '@/request/api.js'

    export default {
        components: {
            slogan,
            company
        },
        data () {
            return {
                direction: '0',
                tip_content: '',
                show_tip_modal: false
            }
        },
        mounted () {
            // setTimeout(function() {
            //     var player = new EZUIKit.EZUIPlayer('myplayer');
            //     // player.play();
            // },5)
        },
        methods: {
            closeModal () {
                this.show_tip_modal = false;
            },
            goLeft () {
                this.direction = '2';
                this.camearStart('2');
            },
            goRight () {
                this.direction = '3';
                this.camearStart('3');
            },
            goUp () {
                this.direction = '0';
                this.camearStart('0');
            },
            goDown () {
                this.direction = '1';
                this.camearStart('1');
            },
            zoomIn () {
                this.direction = '8';
                this.camearStart('8');
            },
            zoomOut () {
                this.direction = '9';
                this.camearStart('9');
            },
            camearStart (direction) {
                let that = this;
                let data = {
                    direction: direction
                }
                camearStart(data).then((res)=> {
                    if(res.code != 200) {
                        that.show_tip_modal = true;
                        that.tip_content = res.msg;
                    }
                })
            },
            camearStop () {
                let that = this;
                let data = {
                    direction: this.direction
                }
                camearStop(data).then((res)=> {
                    
                })
            }
        },
    }
</script>

