<!-- 联系我们 -->
<template>
    <div class="contact">
        <slogan />
        <div class="content">
            <div id="map" class="map"></div>
            <img src="./../assets/img/contact.jpg" alt="">
            <div class="ways" v-html="content"></div>
        </div>
        <company />
    </div>
</template>

<script>
    import slogan from '@/components/slogan.vue'
    import company from '@/components/company.vue'
    import { contactUs } from '@/request/api.js'

    export default {
        components: {
            slogan,
            company
        },
        created () {
            window.scrollTo(0,0);
            this.contactUs()
        },
        mounted () {
            // 实例高德地图
            var map = new AMap.Map('map',{
                zoom: 15,
                center: [114.921078,36.448404]//new AMap.LngLat()
            });
            var marker = new AMap.Marker({
                position: [114.921078,36.448404],  //标记的地图坐标 
                map: map
            });
        },
        data () {
            return {
                info: {}
            }
        },
        computed: {
            content () {
                if(this.info.content) {
                    return this.showHtml(this.info.content);
                }
                
            }
        },
        methods: {
            contactUs () {
                let that = this;
                let data = {};
                contactUs(data).then(res => {
                    if(res.code == 200) {
                        that.info = res.data
                    }
                })
            },
            showHtml:function(str){
                return str
                .replace(str ? /&(?!#?\w+;)/g : /&/g, '&amp;')
                .replace(/&lt;/g,"<")
                .replace(/&gt;/g,">")
                .replace(/&quot;/g,"\"")
                .replace(/&#39;/g, "\'")
                .replace(/&amp;nbsp;/g, "");
            }
        },
    }
</script>

