<!-- 搜索结果 -->
<template>
    <div class="category">
        <div class="fix-top">
            <!-- 搜索框 -->
            <div class="top">
                <div class="search">
                    <img src="./../assets/img/home/search_icon.png" alt="">
                    <form action="javascript:return true;" @keyup="searchShixunshi">
                        <input type="search" placeholder="输入产品名称关键字" v-model="search_content">
                    </form>
                </div>
            </div>
        </div>
        <p class="result-title">搜索结果</p>
        <!-- 商品 -->
        <div class="products">
            <!-- 商品 -->
            <div class="product" v-if="product_list.length > 0 || !product_list">
                <div class="product-item" v-for="item in product_list" :key="item.id" @click="toIntroduce(item.id)">
                    <img :src="item.image" alt="">
                    <div class="msg">
                        <h1>{{item.laboratory_name}}</h1>
                        <p>{{item.introduction}}</p>
                    </div>
                </div>
            </div>
            <p v-else class="no-result">没有搜索到您要查询的产品</p>
        </div>
        <!-- 关注公众号 -->
        <company />
    </div>
</template>

<script>
    import Swiper from 'swiper';
    import company from '@/components/company.vue';
    import { searchShixunshi } from '@/request/api.js'

    export default {
        components : {
            company
        },
        data () {
            return {
                search_content: '',
                product_list: [],
            }
        },
        created () {
            
        },
        mounted () {
            let content = this.$route.query.content;
            this.search_content = content;
            this.searchShixunshi();
        },
        methods: {
            searchShixunshi () {
                let that = this;
                let data = {
                    condition: this.search_content
                }
                searchShixunshi(data).then(res => {
                    if(res.code == 200) {
                        that.product_list = res.data;
                    }else {
                        that.product_list = [];
                    }
                })
            },
            toIntroduce (data_id) {
                // type: 1.产品 2.实训室 3.案例 4.视频
                this.$router.push({
                    path: '/introduce?type=2&id=' + data_id
                })
            }
        }
    }
</script>

