<script>
    import Swiper from 'swiper';
    import company from '@/components/company.vue';
    import { searchVideo } from '@/request/api.js'

    export default {
        components : {
            company
        },
        data () {
            return {
                search_content: '',
                product_list: []
            }
        },
        created () {
            
        },
        mounted () {
            let content = this.$route.query.content;
            this.search_content = content;
            this.searchVideo();
        },
        methods: {
            searchVideo () {
                let that = this;
                let data = {
                    condition: this.search_content
                }
                searchVideo(data).then(res => {
                    if(res.code == 200) {
                        that.product_list = res.data;
                    }else {
                        that.product_list = [];
                    }
                })
            },
            toIntroduce (data_id) {
                // type: 1.产品 2.实训室 3.案例 4.视频
                this.$router.push({
                    path: '/introduce?type=4&id=' + data_id
                })
            }
        }
    }
</script>

