<script>
    import { productInfo, shixunshiInfo, anliInfo, videoInfo } from '@/request/api.js'
    export default {
        data () {
            return {
                detail_type: 0,
                detail_id: 0,
                info: {}
            }
        },
        mounted () {
            window.scrollTo(0,0);
            let query = this.$route.query;
            // type 1.产品 2.实训室 3.案例 4.视频
            this.detail_type = query.type;
            this.detail_id = query.id;
            console.log(this.detail_type)

            if(this.detail_type == '2') {
                this.shixunshiInfo(this.detail_id);
            }else if(this.detail_type == '3') {
                this.anliInfo(this.detail_id)
            }else if(this.detail_type == '4') {
                this.videoInfo(this.detail_id)
            }
        },
        computed: {
            content () {
                if(this.info.content) {
                    return this.showHtml(this.info.content);
                }
                
            }
        },
        methods: {
            // productInfo (product_id) {
            //     let that = this;
            //     let data = {
            //         product_id: product_id
            //     }
            //     productInfo(data).then(res => {
            //         console.log(res)
            //         if(res.code == 200) {
            //             that.info = res.data;
            //         }
            //     })
            // },
            shixunshiInfo (laboratory_id) {
                let that = this;
                let data = {
                    laboratory_id: laboratory_id
                }
                shixunshiInfo(data).then(res => {
                    console.log(res)
                    if(res.code == 200) {
                        that.info = res.data;
                        that.info.title = res.data.laboratory_name;
                    }
                })
            },
            anliInfo (case_id) {
                let that = this;
                let data = {
                    case_id: case_id
                }
                anliInfo(data).then(res => {
                    console.log(res)
                    if(res.code == 200) {
                        that.info = res.data;
                    }
                })
            },
            videoInfo (video_id) {
                let that = this;
                let data = {
                    video_id: video_id
                }
                videoInfo(data).then(res => {
                    if(res.code == 200) {
                        that.info = res.data;
                        that.info.title = res.data.video_name;
                        that.info.content = res.data.introduction;
                    }
                })
            },
            showHtml:function(str){
                return str
                .replace(str ? /&(?!#?\w+;)/g : /&/g, '&amp;')
                .replace(/&lt;/g,"<")
                .replace(/&gt;/g,">")
                .replace(/&quot;/g,"\"")
                .replace(/&#39;/g, "\'")
                .replace(/&amp;nbsp;/g, "");
            }
        },
    }
</script>

