<template>
    <div class="feedback">
        <slogan />
        <img class="pic" src="./../assets/img/feedback.jpg" alt="">
        <div class="forms">
            <input v-model="info.name" type="text" placeholder="姓名">
            <input v-model="info.tel" type="text" placeholder="电话">
            <input v-model="info.email" type="text" placeholder="Email">
            <input v-model="info.title" type="text" placeholder="标题">
            <textarea v-model="info.content" placeholder="信息内容"></textarea>
            <div class="submit" @click="feedback">提交</div>
        </div>
        <company />
        <div class="modal" v-show="show_tip_modal">
            <div class="tip-box">
                <div class="tip" :class="is_success?'success':''">{{tip_content}}</div>
                <div class="cancel" @click="closeModal">确定</div>
            </div>
        </div>
    </div>
</template>

<script>
    import slogan from '@/components/slogan.vue'
    import company from '@/components/company.vue'
    import {feedback} from '@/request/api.js'

    export default {
        components: {
            slogan,
            company
        },
        data () {
            return {
                info: {
                    name: '',
                    tel: '',
                    email: '',
                    title: '',
                    content: ''
                },
                is_success: false,
                tip_content: '',
                show_tip_modal: false
            }
        },
        methods: {
            feedback () {
                let that = this;
                let data = this.info;
                console.log(data);
                that.is_success = false;
                if (!data.name) {
                    that.show_tip_modal = true;
                    that.tip_content = '请填写您的姓名';
                    return;
                }
                if (!data.tel) {
                    that.show_tip_modal = true;
                    that.tip_content = '请填写您的电话号码';
                    return;
                }
                if (!data.email) {
                    that.show_tip_modal = true;
                    that.tip_content = '请填写您的邮箱地址';
                    return;
                }
                if (!data.title) {
                    that.show_tip_modal = true;
                    that.tip_content = '请填写反馈信息的标题';
                    return;
                }
                if (!data.content) {
                    that.show_tip_modal = true;
                    that.tip_content = '请填写反馈信息';
                    return;
                }
                feedback(data).then(res => {
                    console.log(res)
                    if(res.code == 200) {
                        that.is_success = true;
                        that.tip_content = '反馈成功';
                    }else {
                        that.is_success = false;
                        that.tip_content = res.msg;
                    }
                    that.show_tip_modal = true;
                })
            },
            closeModal () {
                this.show_tip_modal = false;
            }
        },
    }
</script>

